"use client";

import { motion } from "framer-motion";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import JsonLd from "@/components/seo/JsonLd";
import { generateFAQSchema } from "@/lib/seo";

const faqData = [
  {
    question: "–°–∫–æ–ª—å–∫–æ —Å—Ç–æ–∏—Ç –∫—É—Ö–Ω—è –Ω–∞ –∑–∞–∫–∞–∑ –≤ –¢–≤–µ—Ä–∏?",
    answer:
      "–°—Ç–æ–∏–º–æ—Å—Ç—å –∫—É—Ö–Ω–∏ –Ω–∞ –∑–∞–∫–∞–∑ –∑–∞–≤–∏—Å–∏—Ç –æ—Ç —Ä–∞–∑–º–µ—Ä–æ–≤, –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö –º–∞—Ç–µ—Ä–∏–∞–ª–æ–≤ –∏ —Ñ—É—Ä–Ω–∏—Ç—É—Ä—ã. –ù–∞—à–∏ —Ü–µ–Ω—ã –Ω–∞—á–∏–Ω–∞—é—Ç—Å—è –æ—Ç 25 000 ‚ÇΩ –∑–∞ –ø–æ–≥–æ–Ω–Ω—ã–π –º–µ—Ç—Ä. –î–ª—è —Ç–æ—á–Ω–æ–≥–æ —Ä–∞—Å—á—ë—Ç–∞ –æ—Å—Ç–∞–≤—å—Ç–µ –∑–∞—è–≤–∫—É ‚Äî –º—ã –ø–æ–¥–≥–æ—Ç–æ–≤–∏–º –∏–Ω–¥–∏–≤–∏–¥—É–∞–ª—å–Ω–æ–µ –ø—Ä–µ–¥–ª–æ–∂–µ–Ω–∏–µ –±–µ—Å–ø–ª–∞—Ç–Ω–æ.",
  },
  {
    question: "–ö–∞–∫–∏–µ —Å—Ä–æ–∫–∏ –∏–∑–≥–æ—Ç–æ–≤–ª–µ–Ω–∏—è –∫—É—Ö–Ω–∏?",
    answer:
      "–°—Ä–µ–¥–Ω–∏–π —Å—Ä–æ–∫ –∏–∑–≥–æ—Ç–æ–≤–ª–µ–Ω–∏—è –∫—É—Ö–Ω–∏ ‚Äî 20 —Ä–∞–±–æ—á–∏—Ö –¥–Ω–µ–π —Å –º–æ–º–µ–Ω—Ç–∞ –ø–æ–¥–ø–∏—Å–∞–Ω–∏—è –¥–æ–≥–æ–≤–æ—Ä–∞ –∏ —É—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è –ø—Ä–æ–µ–∫—Ç–∞. –°—Ä–æ–∫–∏ –º–æ–≥—É—Ç –≤–∞—Ä—å–∏—Ä–æ–≤–∞—Ç—å—Å—è –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Å–ª–æ–∂–Ω–æ—Å—Ç–∏ –ø—Ä–æ–µ–∫—Ç–∞ –∏ –∑–∞–≥—Ä—É–∂–µ–Ω–Ω–æ—Å—Ç–∏ –ø—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–∞.",
  },
  {
    question: "–î–æ—Å—Ç–∞–≤–∫–∞ –∏ —É—Å—Ç–∞–Ω–æ–≤–∫–∞ –≤—Ö–æ–¥—è—Ç –≤ —Å—Ç–æ–∏–º–æ—Å—Ç—å?",
    answer:
      "–î–∞, –¥–æ—Å—Ç–∞–≤–∫–∞ –∏ –ø—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª—å–Ω–∞—è —É—Å—Ç–∞–Ω–æ–≤–∫–∞ –≤–∫–ª—é—á–µ–Ω—ã –≤ —Å—Ç–æ–∏–º–æ—Å—Ç—å –∫—É—Ö–Ω–∏ –ø–æ –¢–≤–µ—Ä–∏ –∏ –±–ª–∏–∂–∞–π—à–∏–º –ø—Ä–∏–≥–æ—Ä–æ–¥–∞–º. –î–ª—è —É–¥–∞–ª—ë–Ω–Ω—ã—Ö —Ä–∞–π–æ–Ω–æ–≤ –¢–≤–µ—Ä—Å–∫–æ–π –æ–±–ª–∞—Å—Ç–∏ –¥–æ—Å—Ç–∞–≤–∫–∞ —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ—Ç—Å—è –æ—Ç–¥–µ–ª—å–Ω–æ.",
  },
  {
    question: "–ö–∞–∫–∏–µ –º–∞—Ç–µ—Ä–∏–∞–ª—ã –≤—ã –∏—Å–ø–æ–ª—å–∑—É–µ—Ç–µ?",
    answer:
      "–ú—ã –∏—Å–ø–æ–ª—å–∑—É–µ–º –µ–≤—Ä–æ–ø–µ–π—Å–∫—É—é —Ñ—É—Ä–Ω–∏—Ç—É—Ä—É Blum –∏ Hettich, –ú–î–§ –∏ –º–∞—Å—Å–∏–≤ –¥–µ—Ä–µ–≤–∞ –æ—Ç –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã—Ö –ø–æ—Å—Ç–∞–≤—â–∏–∫–æ–≤, —Å—Ç–æ–ª–µ—à–Ω–∏—Ü—ã –∏–∑ –∏—Å–∫—É—Å—Å—Ç–≤–µ–Ω–Ω–æ–≥–æ –∫–∞–º–Ω—è, –∫–≤–∞—Ä—Ü–∞ –∏ –Ω–∞—Ç—É—Ä–∞–ª—å–Ω–æ–≥–æ –º—Ä–∞–º–æ—Ä–∞. –í—Å–µ –º–∞—Ç–µ—Ä–∏–∞–ª—ã —Å–µ—Ä—Ç–∏—Ñ–∏—Ü–∏—Ä–æ–≤–∞–Ω—ã –∏ —ç–∫–æ–ª–æ–≥–∏—á–µ—Å–∫–∏ –±–µ–∑–æ–ø–∞—Å–Ω—ã.",
  },
  {
    question: "–ï—Å—Ç—å –ª–∏ –≥–∞—Ä–∞–Ω—Ç–∏—è –Ω–∞ –∫—É—Ö–Ω—é?",
    answer:
      "–ú—ã –ø—Ä–µ–¥–æ—Å—Ç–∞–≤–ª—è–µ–º –≥–∞—Ä–∞–Ω—Ç–∏—é 2 –≥–æ–¥–∞ –Ω–∞ –≤—Å–µ –∏–∑–¥–µ–ª–∏—è –∏ –≤—ã–ø–æ–ª–Ω–µ–Ω–Ω—ã–µ —Ä–∞–±–æ—Ç—ã. –ì–∞—Ä–∞–Ω—Ç–∏—è —Ä–∞—Å–ø—Ä–æ—Å—Ç—Ä–∞–Ω—è–µ—Ç—Å—è –Ω–∞ –º–µ–±–µ–ª—å–Ω—ã–µ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ü–∏–∏, —Ñ–∞—Å–∞–¥—ã, —Ñ—É—Ä–Ω–∏—Ç—É—Ä—É –∏ –º–æ–Ω—Ç–∞–∂. –ü—Ä–∏ –≤–æ–∑–Ω–∏–∫–Ω–æ–≤–µ–Ω–∏–∏ –ø—Ä–æ–±–ª–µ–º –Ω–∞—à –º–∞—Å—Ç–µ—Ä –ø—Ä–∏–µ–¥–µ—Ç –±–µ—Å–ø–ª–∞—Ç–Ω–æ.",
  },
  {
    question: "–ú–æ–∂–Ω–æ –ª–∏ –æ—Ñ–æ—Ä–º–∏—Ç—å —Ä–∞—Å—Å—Ä–æ—á–∫—É?",
    answer:
      "–î–∞, –º—ã –ø—Ä–µ–¥–ª–∞–≥–∞–µ–º —Ä–∞—Å—Å—Ä–æ—á–∫—É –±–µ–∑ –ø–µ—Ä–µ–ø–ª–∞—Ç –Ω–∞ —Å—Ä–æ–∫ –¥–æ 12 –º–µ—Å—è—Ü–µ–≤. –¢–∞–∫–∂–µ –≤–æ–∑–º–æ–∂–Ω–∞ –æ–ø–ª–∞—Ç–∞ –≤ –Ω–µ—Å–∫–æ–ª—å–∫–æ —ç—Ç–∞–ø–æ–≤: –ø—Ä–µ–¥–æ–ø–ª–∞—Ç–∞ –ø—Ä–∏ –∑–∞–∫–ª—é—á–µ–Ω–∏–∏ –¥–æ–≥–æ–≤–æ—Ä–∞, –æ–ø–ª–∞—Ç–∞ –ø—Ä–∏ –¥–æ—Å—Ç–∞–≤–∫–µ –∏ –ø–æ—Å–ª–µ —É—Å—Ç–∞–Ω–æ–≤–∫–∏.",
  },
  {
    question: "–ö–∞–∫ –ø—Ä–æ—Ö–æ–¥–∏—Ç –ø—Ä–æ—Ü–µ—Å—Å –∑–∞–∫–∞–∑–∞?",
    answer:
      "–ü—Ä–æ—Ü–µ—Å—Å –∑–∞–∫–∞–∑–∞ –≤–∫–ª—é—á–∞–µ—Ç 5 —ç—Ç–∞–ø–æ–≤: 1) –ë–µ—Å–ø–ª–∞—Ç–Ω—ã–π –∑–∞–º–µ—Ä ‚Äî –Ω–∞—à —Å–ø–µ—Ü–∏–∞–ª–∏—Å—Ç –ø—Ä–∏–µ–∑–∂–∞–µ—Ç –∫ –≤–∞–º. 2) 3D-–ø—Ä–æ–µ–∫—Ç ‚Äî –≤—ã –≤–∏–¥–∏—Ç–µ –±—É–¥—É—â—É—é –∫—É—Ö–Ω—é –≤ –æ–±—ä—ë–º–µ. 3) –î–æ–≥–æ–≤–æ—Ä –∏ –Ω–∞—á–∞–ª–æ –ø—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–∞. 4) –ò–∑–≥–æ—Ç–æ–≤–ª–µ–Ω–∏–µ –∑–∞ 20 –¥–Ω–µ–π. 5) –î–æ—Å—Ç–∞–≤–∫–∞ –∏ –ø—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª—å–Ω–∞—è —É—Å—Ç–∞–Ω–æ–≤–∫–∞.",
  },
  {
    question: "–†–∞–±–æ—Ç–∞–µ—Ç–µ –ª–∏ –≤—ã –ø–æ –≤—Å–µ–π –¢–≤–µ—Ä—Å–∫–æ–π –æ–±–ª–∞—Å—Ç–∏?",
    answer:
      "–î–∞, –º—ã —Ä–∞–±–æ—Ç–∞–µ–º –ø–æ –≤—Å–µ–π –¢–≤–µ—Ä—Å–∫–æ–π –æ–±–ª–∞—Å—Ç–∏: –¢–≤–µ—Ä—å, –¢–æ—Ä–∂–æ–∫, –†–∂–µ–≤, –ö–æ–Ω–∞–∫–æ–≤–æ, –ö–∏–º—Ä—ã, –í—ã—à–Ω–∏–π –í–æ–ª–æ—á—ë–∫, –ë–µ–∂–µ—Ü–∫, –ù–µ–ª–∏–¥–æ–≤–æ –∏ –¥—Ä—É–≥–∏–µ –≥–æ—Ä–æ–¥–∞. –ó–∞–º–µ—Ä –∏ –¥–æ—Å—Ç–∞–≤–∫–∞ –¥–æ—Å—Ç—É–ø–Ω—ã –≤–æ –≤—Å–µ –Ω–∞—Å–µ–ª—ë–Ω–Ω—ã–µ –ø—É–Ω–∫—Ç—ã –æ–±–ª–∞—Å—Ç–∏.",
  },
];

const FAQSection = () => {
  return (
    <section id="faq" className="section-padding">
      <div className="container-custom">
        {/* JSON-LD FAQPage schema */}
        <JsonLd data={generateFAQSchema(faqData)} />

        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <span className="inline-block px-4 py-2 rounded-full bg-primary/10 text-primary text-sm font-medium mb-4">
            –í–æ–ø—Ä–æ—Å—ã –∏ –æ—Ç–≤–µ—Ç—ã
          </span>
          <h2 className="text-3xl md:text-4xl lg:text-5xl font-bold mb-4">
            –ß–∞—Å—Ç–æ –∑–∞–¥–∞–≤–∞–µ–º—ã–µ –≤–æ–ø—Ä–æ—Å—ã
          </h2>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            –û—Ç–≤–µ—Ç—ã –Ω–∞ –ø–æ–ø—É–ª—è—Ä–Ω—ã–µ –≤–æ–ø—Ä–æ—Å—ã –æ –∑–∞–∫–∞–∑–µ –∫—É—Ö–æ–Ω—å –≤ TverKuhni
          </p>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5 }}
          className="max-w-3xl mx-auto"
        >
          <Accordion type="single" collapsible className="space-y-4">
            {faqData.map((item, index) => (
              <AccordionItem
                key={index}
                value={`faq-${index}`}
                className="premium-card px-6 border-none"
              >
                <AccordionTrigger className="text-left font-semibold hover:no-underline py-5">
                  {item.question}
                </AccordionTrigger>
                <AccordionContent className="text-muted-foreground pb-5 leading-relaxed">
                  {item.answer}
                </AccordionContent>
              </AccordionItem>
            ))}
          </Accordion>
        </motion.div>
      </div>
    </section>
  );
};

export default FAQSection;
